.Sudoku-page{
    position: absolute;
    top: 15%;
    left: 31.5%;
    table {
            border-collapse: collapse;
            margin-left: auto;
            margin-right: auto;
        }
    
        colgroup,
        tbody {
            border: solid medium;
        }
    
        td {
            border: solid thin;
            height: 2.4em;
            width: 2.4em;
            text-align: center;
            font-size: 30px;
            padding: 0;
        }
    
        input {
            border: solid thin;
            height: 2.4em;
            width: 2.4em;
            text-align: center;
            font-size: 30px;
            padding: 0;
        }
    
        button {
            padding: 15px 32px;
            font-size: 15px;
        }
    
        .options {
            margin: 0;
            position: absolute;
            left: 50%;
            -ms-transform: translate(-50%);
            transform: translate(-50%);
        }
    
        .Title {
            text-align: center;
            font-size: 50px;
        }
}

